/* MemoComponent.css */

:root {
  --card-width: 100%;
  --card-height: 360px;
  --card-header-height: 40px;
  --card-footer-height: 30px;
  --card-body-height: calc(
    var(--card-height) - var(--card-header-height) - var(--card-footer-height)
  );

  --room-card-background-color: #ffffff;
  --room-card-border: #e0e0e0;
  --room-card-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  --button-gap: 8px;

  /*    */
  --color-accent-coral: #ff6347; /*  (1) */
  --color-primary-deep-blue: #2196f3; /*   (2 ) */
  --color-primary-soft-green: #4caf50; /*   () */
}

/*    */
.memo-container {
  display: flex;
  flex-direction: column;
  /*  () width/height   auto or 100%   */
}

/*   */
.memo-header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #c6c6c6; /* or #919191    */
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px 5px 0 0;
  max-width: 300px;
  /*   :   /  */
  height: 40px;
  flex-shrink: 0;

  /*      */
  margin-bottom: 0;
}

.memo-edit-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.memo-edit-button:hover {
  color: red;
}

/*   */
.memo-body {
  flex: 1; /*      */
  overflow-y: auto; /*   */
  background-color: #ffffff;
  border-radius: 0 0 5px 5px;
  padding: 15px 20px;
}

/*   textarea */
.memo-textarea {
  width: 100%;
  min-height: 0;
  background: transparent;
  border: none;
  outline: none;
  resize: none;
  padding: 5px;
  box-sizing: border-box;
  font-size: 1.2rem;
  line-height: 1.4;
  color: #333;
}

/*     */
.memo-text-display {
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 1rem;
  line-height: 1.4;
  color: #333;
}

/*     */
.memo-button-group {
  display: flex;
  align-items: center;
  gap: var(--button-gap);
  margin-left: auto;
}

/*   */
.memo-button-group .memo-save-button {
  background-color: var(--color-primary-deep-blue);
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.memo-button-group .memo-save-button:hover {
  background-color: #1976d2;
  transform: scale(1.05);
}

/*   */
.memo-button-group .memo-cancel-button {
  background-color: var(--color-accent-coral);
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.memo-button-group .memo-cancel-button:hover {
  background-color: #e5533b;
  transform: scale(1.05);
}
