/*  : 140px , 2(: , : 2) */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 140px;
  display: flex;
  background-color: transparent;
  border-bottom: 1px solid #a7a5a53d;
  z-index: 10;
}

/* ()  :  140px  */
.header-left {
  width: 280px; /*    */
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}
.header-logo-link {
  display: inline-block;
}
.header-logo {
  height: 40px;
}

/* () 2 :  70px,  70px */
.header-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: transparent;
}

/* (1)    */
.header-right-top {
  display: flex;
  align-items: center;
  justify-content: center; /*    */
  height: 70px;
  background-color: transparent;
  gap: 10px;
}

.arrow-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.4rem;
  color: #3f3f3f78;
  transition: color 0.3s ease, transform 0.3s ease;
}
.arrow-button:hover {
  color: #194fcdaa;
  transform: scale(1.1);
}

.selected-date {
  font-size: 2.3rem;
  font-weight: 700;
  color: #050505;
}
.weekday {
  margin-left: 5px;
  font-size: 1.6rem;
  color: #555555;
  margin: 10px;
}
.weekend {
  color: #ff00008e;
}

/* (2) : /// + OTA +  */
.header-right-bottom {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
}
.weekend {
  color: #ff00008e;
}

/* (2) : /// + OTA +  */
.header-right-bottom {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; /* -- */
  height: 70px;
  background-color: transparent;
  align-items: center;
  padding: 0 20px;
}

/*  : /// */
.header-right-bottom-left {
  display: flex;
  align-items: center;
  margin-left: 20px;
  margin-right: 20px;
}
.additional-buttons {
  display: flex;
  gap: 10px;
}
.memo-button {
  padding: 6px 12px;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #f0f0f0;
  color: #333;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.memo-button:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}
.memo-button.active {
  background-color: #d0d0d0;
}

/*  : OTA  */
.header-ota-status {
  display: flex;
  justify-content: center;
  align-items: center;
}
.ota-status-item {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  margin: 0 8px;
}
.status-lamp {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-left: 4px;
}
.status-lamp.green {
  background-color: rgb(131, 239, 131);
}
.status-lamp.gray {
  background-color: gray;
}

/*  :   */
.header-right-bottom-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.quick-create-buttons {
  display: flex;
  gap: 8px;
}
.quick-button {
  padding: 6px 12px;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #6287ace1;
  color: #fff;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.quick-button:hover {
  background-color: #007cb576;
  transform: scale(1.05);
}
.quick-button-green {
  background-color: #4caf4fe9;
}
.quick-button-green:hover {
  background-color: #45a04979;
}
