/* ==========================================================================
   [Reservation Card - New Structure]
========================================================================== */

:root {
  --card-width: 100%;
  --card-height: 370px;
  --card-header-height: 40px;
  --card-footer-height: 30px;
  --card-body-height: calc(
    var(--card-height) - var(--card-header-height) - var(--card-footer-height)
  );

  --room-card-background-color: #ffffff;
  --room-card-border: #e0e0e0;
  --room-card-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  --button-gap: 8px;

  /*    */
  --color-accent-coral: #ff6347; /*  (1) */
  --color-primary-deep-blue: #2196f3; /*   (2 ) */
  --color-primary-soft-green: #4caf50; /*   () */
}

/*     */
.border-primary-soft-green {
  border: 2px solid var(--color-primary-soft-green); /*   () */
}

.border-accent-coral {
  border: 2px solid var(--color-accent-coral); /*  (1) */
}

.border-primary-deep-blue {
  border: 2px solid var(--color-primary-deep-blue); /*   (2 ) */
}

.room-card {
  width: var(--card-width);
  height: var(--card-height);
}

/*  :    */
.room-card-top {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.room-card-head {
  flex: 0 0 var(--card-header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px 8px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #dddddd;
  font-size: 0.9rem;
}

/*  :     */
.room-card-footer {
  flex: 0 0 var(--card-footer-height);
  background-color: #f3f3f3;
  border-top: 1px solid #dddddd;
  text-align: center;
  font-size: 0.85rem;
  padding: 4px;
}

/* ==========================================================================
   [Action Buttons on Reservation Card Header]
========================================================================== */
.room-card-head .action-buttons {
  display: flex;
  gap: 4px;
}

/* ==========================================================================
   [Flip Effect & Other Card Styles]
========================================================================== */
.flip-container {
  perspective: 1000px;
  height: 100%;
  position: relative;
}

.room-card-inner {
  min-width: 320px;
  max-height: 370px;
  box-sizing: border-box;
  height: 100%;
  transition: transform 0.6s ease-in-out;
  transform-style: preserve-3d;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.flip-container.flipped .room-card-inner {
  transform: rotateY(180deg);
}

.room-card-front,
.room-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-color: var(--room-card-background-color);
  -webkit-backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.room-card-front {
  z-index: 2;
  transform: rotateY(0deg);
}

.room-card-back {
  z-index: 1;
  transform: rotateY(180deg);
  padding: 0;
  box-sizing: border-box;
}

.room-card-back::before {
  display: none;
  backface-visibility: hidden;
}

.room-card.flipped:hover,
.room-card.flipped:hover::before {
  transform: none !important;
  background-color: var(--room-card-background-color) !important;
  z-index: auto !important;
}

.flip-container.auto-flip .room-card-inner {
  animation: autoFlip 1s ease-in-out;
}

@keyframes autoFlip {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(-30deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

.flipped .room-card-inner {
  transform: rotateY(180deg);
}

/* ==========================================================================
   [Miscellaneous: Invoice Button, Highlights, etc.]
========================================================================== */
.invoice-icon-button-back {
  background: transparent;
  border: none;
  cursor: pointer;
  color: #666e76;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.3s ease, transform 0.2s ease;
}

.invoice-icon-button-back:hover {
  color: rgb(186, 32, 233);
  transform: scale(1.5);
}

.card-content {
  padding: 8px;
  flex: 1;
}

.site-info-footer {
  margin-top: 5%;
  padding-top: 5px;
  border-top: 1px solid #ddd;
  flex-shrink: 0;
}

.site-info-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  width: 90%;
}

/* ===============================
   [Unassigned Reservations Section]
=============================== */

/*      */
.unassigned-card {
  background-color: #eed142; /*   */
  transform: scale(0.95); /*   */
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

/*       */
.unassigned-card:hover {
  transform: scale(0.98);
  background-color: #eed142; /*   */
}

.unassigned-section {
  border: 1px solid #ddd;
  background-color: #f9f9f9;
  border-radius: 6px;
  padding: 10px;
  margin-bottom: 20px;
}

.unassigned-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.unassigned-header h3 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
}

.unassigned-header-title-button {
  background-color: #cccccc;
  color: #333;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.unassigned-header-title-button:hover {
  background-color: #aaaaaa;
}

/*     */
.unassigned-list {
  display: flex; /*   */
  flex-wrap: wrap; /*    */
  gap: 10px; /*    */
  align-items: flex-start; /*     */
}

/*    DraggableReservationCard  ,
            */
.unassigned-list .room-card {
  width: 320px; /*   ( ) */
  min-height: 320px; /*   ( ) */
  margin: 0;
  padding: 0; /*    */
  scale: 0.95; /*   */
}

/* ==========================================================================
   [Highlight Animations]
========================================================================== */
.room-card.onsite-created {
  animation: highlightPulse 10s;
  background-color: rgb(220, 249, 220);
}

@keyframes highlightPulse {
  0% {
    box-shadow: 0 0 50px #1b1b1b;
  }
  100% {
    box-shadow: 0 0 0 #c4f4c3;
  }
}

.room-card.highlighted {
  border: 3px solid #00ff2f;
  animation: highlightBlink 1s infinite;
}

@keyframes highlightBlink {
  0% {
    box-shadow: 0 0 10px #ff0000;
  }
  50% {
    box-shadow: 0 0 20px #ff0000;
  }
  100% {
    box-shadow: 0 0 10px #ff0000;
  }
}


.checked-out-count {
  font-style: italic;
  font-size: 1.4rem;
  color: red;
}

.unassigned-reservations-list_all {
  margin: 5px;
}